<script>
    import api from '../../api/user'
    import type from '../../assets/type'
    import imgUpload from '../../components/imgUpload'

    export default {
        name: "userInfo",
        components: {imgUpload},
        data() {
            return {
                user: {},
                disable: true,
                userData: {},
                stages: [{value: ''}, {value: ''}, {value: ''}, {value: ''}, {value: ''}, {value: ''}, {value: ''}, {value: ''}, {value: ''}],
                value: null,
                role: [],
                industries: [{value: ''}, {value: ''}, {value: ''}, {value: ''}, {value: ''}],
                service: [{value: ''}, {value: ''}, {value: ''}, {value: ''}, {value: ''}, {value: ' '}, {value: ''}, {value: ''}, {value: ''}],
                data: {},
                logo: null,
                license_scan: null,

            }
        },

        mounted() {
            this.loadData();
            this.role = type.role;
            // this.disable = false;
            this.industry = type.industry;
        },
        methods: {
            loadData() {
                let _this = this;
                api.info(res => {
                    this.data = res.data.data;
                    if (this.data.auditor === 0) {
                        this.$confirm(',,', '', {
                            confirmButtonText: '',
                            cancelButtonText: '',
                            type: 'warning'
                        }).then(() => {
                            _this.disable = false;
                        }).catch(() => {
                        });
                    }

                }, () => {
                })

            },
            editEd() {
                this.disable = false;
            },
            update() {
                if (!api.isStrValid(this.data.company)) {
                    this.$message.error('');
                    return;
                }
                if (!api.isStrValid(this.data.stage)) {
                    this.$message.error('');
                    return;
                }
                if (!api.isStrValid(this.data.website)) {
                    this.$message.error('');
                    return;
                }
                if (!api.isStrValid(this.data.industry)) {
                    this.$message.error('');
                    return;
                }
                if (!api.isStrValid(this.data.legal_person)) {
                    this.$message.error('/');
                    return;
                }
                if (!api.isStrValid(this.data.duty)) {
                    this.$message.error('');
                    return;
                }
                if (!api.isStrValid(this.data.mobile)) {
                    this.$message.error('');
                    return;
                }
                if (!api.isStrValid(this.data.phone)) {
                    this.$message.error('');
                    return;
                }
                if (!api.isStrValid(this.data.email)) {
                    this.$message.error('');
                    return;
                }
                if (!api.isStrValid(this.data.register_address)) {
                    this.$message.error('');
                    return;
                }
                if (!api.isStrValid(this.data.business_license)) {
                    this.$message.error('');
                    return;
                }
                if (!api.isStrValid(this.data.register_capital)) {
                    this.$message.error('');
                    return;
                }
                if (this.license_scan == null) {
                    this.$message.error('');
                    return;
                }
                if (!api.isStrValid(this.data.mailing_address)) {
                    this.$message.error('');
                    return;
                }
                if (!api.isStrValid(this.data.sales_volume)) {
                    this.$message.error('');
                    return;
                }
                if (!api.isStrValid(this.data.total_people)) {
                    this.$message.error('');
                    return;
                }
                if (!api.isStrValid(this.data.developer_people)) {
                    this.$message.error('');
                    return;
                }
                if (!api.isStrValid(this.data.patent)) {
                    this.$message.error('');
                    return;
                }
                if (!api.isStrValid(this.data.high_technology)) {
                    this.$message.error('');
                    return;
                }
                if (!api.isStrValid(this.data.service_direction)) {
                    this.$message.error('');
                    return;
                }
                if (!api.isStrValid(this.data.products_introduce)) {
                    this.$message.error('');
                    return;
                }
                if (!api.isStrValid(this.data.business_introduce)) {
                    this.$message.error(''
                    );
                    return;
                }
                if (this.logo == null) {
                    this.$message.error('logo');
                    return;
                }
                let data = api.objToFormData(this.data);

                data.append('logo',this.logo);
                data.append('license_scan',this.license_scan);
                api.updateInfo(data, () => {
                    this.$message.success('');
                    this.disable = true;
                });
            },
            cancel() {
                this.disable = true;
                this.setData();
            },
        }
    }
</script>

